
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
h1 {
    font-size: 1.8em;
    margin: 0 0 .5em 0;
}
h2 {
	font-weight: 600;
	font-style: italic;
}
img.me {
  height: 120px;
  border-radius: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  margin-top: 260px;
}
#inner {
	height: 400px;
	width: 400px;
	padding: 12px;
	position: absolute;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	/* background-color: #eee; */
	background-image: url(/static/img/concrete-texture.3eb73e5.png);
	opacity: .8;
	overflow: auto;
	border-radius: 4px;
}
#wrapper {
	height: 100%;
}
#buttons {
	position: absolute;
	top: 43%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	margin-left: -270px;
}
#buttons ul {
	list-style: none;
}
#buttons li {
	margin: 1em 0;
}
button {
	color: rgb(247, 210, 210);
	font-size: 20px;
	font-weight: 500;
	width: 60px;
	height: 60px;
	border-radius: 60px;
	border: none;
	cursor: pointer;
	-webkit-transition: background 3s;
	transition: background 3s;
}
button:focus {
	outline: none;
}










































































































































